import React, { useState } from "react";
import validator from "validator";
import axios from "axios";
import { TextField, Button } from "@material-ui/core";

function CopyButton() {
  return (
    <Button size="small" color="secondary" variant="contained">
      copy
    </Button>
  );
}

function ShortenForm() {
  const [longUrl, setLongUrl] = useState("");
  const [shortUrl, setShortUrl] = useState("");

  const handleLongUrl = e => {
    setLongUrl(e.target.value);
  };

  const handleSubmit = e => {
    e.preventDefault();

    const validURL = validator.isURL(longUrl, {
      require_protocol: true,
    });

    if (!validURL) {
      alert("Please ensure this url is correct and includes http(s) protocol");
    } else {
      axios
        .post("/api/shorten", {
          url: longUrl,
        })
        .then(res => {
          setShortUrl(
            `https://url-shortener-gol.herokuapp.com/${res.data.hash}`
          );
        })
        .catch(err => console.log(err));
    }
  };
  return (
    <div>
      <form className="shortenForm" onSubmit={handleSubmit}>
        {/* <input
          type="text"
          placeholder="Enter URL with http(s)"
          onChange={handleLongUrl}
          value={longUrl}
        /> */}
        <TextField
          placeholder="Enter URL with http(s)"
          variant="filled"
          onChange={handleLongUrl}
          value={longUrl}
        ></TextField>
        <Button
          color="primary"
          variant="contained"
          type="submit"
          value="shorten"
          size="large"
          className="shortenButton"
        >
          Shorten
        </Button>

        <TextField
          variant="filled"
          size="small"
          value={shortUrl ? shortUrl : ""}
          InputProps={{
            endAdornment: <CopyButton />,
          }}
        ></TextField>
      </form>
    </div>
  );
}

export default ShortenForm;
